.dashboard-wrapper {
    background-color: #f5f5f5;
    background-image: url('https://images.wallpaperscraft.com/image/single/mountains_sunset_art_146043_1920x1080.jpg');
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: left;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-x: hidden;
}

.center {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
  }
  
  .subBtn, .csvbtn {
    width: 300px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .subBtn button, .csvbtn button {
    text-decoration: none;
    border: 2px solid #010100;
    padding: 15px;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 2px;
    position: relative;
    display: inline-block;
  }
  
  .subBtn span, .csvbtn span {
    font-size: 25px;
    font-weight: bold;
    font-family: "SF Pro Display", sans-serif;
    position: relative;
    z-index: 3;
  }
  
  .subBtn button:hover::before, 
  .csvbtn button:hover::before {
    transform: scaleY(0);
  }
  
  .subBtn button:hover::after,
  .csvbtn button:hover::after {
    transform: scaleX(0);
  }
  
  .subBtn button {
    color: #ffffff;
    transition: all 0.5s ease-in-out;
  }
  
  .subBtn button:hover,
  .csvbtn button:hover {
    color: #000000;
    transition: all 0.5s ease-in-out;
    background-color: #ffc23e;
  }
  
  .subBtn button::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    transition: all 0.5s ease-in-out;
    transform: scaleY(1);
  }
  
  .subBtn button::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    /* width: 100%;
    height: 100%; */
    background-color: black;
    transition: all 0.5s ease-in-out;
    transform: scaleX(1);
  }

  

.center2 {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
  }

  .csvbtn button::before {
    content: "";
    position: absolute;
    top: 5px;
    left: -2px;
    width: calc(100% + 6px);
    height: calc(100% - 10px);
    background-color: #ffffff;
    transition: all 0.5s ease-in-out;
    transform: scaleY(1);
  }
  .csvbtn button::after {
    content: "";
    position: absolute;
    left: 5px;
    top: -5px;
    width: calc(100% - 10px);
    height: calc(100% + 10px);
    background-color: #ffffff;
    transition: all 0.5s ease-in-out;
    transform: scaleX(1);
  }